import React from 'react'
import { Select } from 'antd'
import { SelectProps } from 'antd/lib/select'
import styled from 'styled-components'

const StyledSelect: any = styled(Select)`
  &.ant-select {
    &.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
    .ant-select-selection:hover,
    .ant-select-selection:active,
    .ant-select-selection:focus {
      border-color: ${p => p.theme.primary};
    }

    .ant-select-selection {
      border-color: ${p => p.theme.secondary};
    }

    .ant-select-selection:active,
    .ant-select-selection:focus {
      box-shadow: none;
    }

    .ant-select-arrow {
      color: ${p => p.theme.primary};
    }

    .ant-select-arrow-icon {
      position: relative;
      top: -1px;
      font-size: 14px;
    }

    &.ant-select-borderless .ant-select-selector {
      position: relative;
      bottom: 1px;
      border: 0;
      border-bottom: 1px solid ${p => p.theme.primary} !important;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  &.ant-select.is--transparent {
    &.ant-select-focused > .ant-select-selector {
      border: 1px solid #666a8f;
    }

    > .ant-select-selector {
      background: transparent;
      border: 1px solid #666a8f;
      color: #a9b6d8;

      &:active,
      &:focus,
      &:hover {
        border: 1px solid #666a8f;
      }
    }

    .ant-select-arrow {
      color: #a9b6d8;
    }
  }
`

class SelectComponent<T = any> extends React.Component<SelectProps<T>, any> {
  public static Option = Select.Option

  public static displayName = 'Select'

  render() {
    return <StyledSelect {...this.props}>{this.props.children}</StyledSelect>
  }
}

export default SelectComponent
