import React from 'react'
import { Form } from 'antd'
import { ColumnProps } from 'antd/lib/table'
import { FormInstance } from 'antd/lib/form'
import { concat, __, pathOr, invoker, filter } from 'ramda'
import Input from '@/components/input'
import Checkbox from '@/components/checkbox'
import EditableTable from '@/components/editable-table'
import { uuid } from '@/utils/webHelper'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting } from '@/constants/types'
import { Modal, ModalProps } from './report-template-modal'
const { compose } = require('ramda')

export interface Setting extends ReportTemplateSetting {
  options?: Option[]
}

export type Option = {
  id: React.Key
  text?: string
  selected?: boolean
  freetext?: boolean
}

export const defaultValue: Setting = {
  options: [
    {
      id: uuid(),
      text: '預設選項 1',
      selected: true,
      freetext: false,
    },
    {
      id: uuid(),
      text: '預設選項 2',
      selected: true,
      freetext: false,
    },
    {
      id: uuid(),
      text: '預設選項 3',
      selected: true,
      freetext: false,
    },
  ],
}

export function TemplateSelectionModal({
  onCancel,
  onOk,
  item,
  ...props
}: ModalProps) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  const handleRowAdd = (form: FormInstance) => {
    return compose(
      concat(__, [
        {
          id: uuid(),
          text: undefined,
          selected: true,
          freetext: false,
        },
      ]),
      pathOr([], ['options']),
      invoker(0, 'getFieldsValue')()
    )(form)
  }

  const handleRowDelete = ({
    id,
    form,
  }: {
    id: React.Key
    form: FormInstance
  }) => {
    return compose(
      filter((x: any) => x.id !== id),
      pathOr([], ['options']),
      invoker(0, 'getFieldsValue')()
    )(form)
  }

  const columns: ColumnProps<Option>[] = [
    {
      title: '選項名稱',
      render: (v, r, idx) => (
        <Form.Item noStyle name={['options', idx, 'text']}>
          <Input />
        </Form.Item>
      ),
    },
    {
      title: '是否可選',
      align: 'center',
      render: (v, r, idx) => (
        <Form.Item
          noStyle
          name={['options', idx, 'selected']}
          valuePropName="checked">
          <Checkbox />
        </Form.Item>
      ),
    },
    {
      title: '是否可輸入',
      align: 'center',
      render: (v, r, idx) => (
        <Form.Item
          noStyle
          name={['options', idx, 'freetext']}
          valuePropName="checked">
          <Checkbox />
        </Form.Item>
      ),
    },
  ]

  return (
    <Modal
      {...props}
      title={
        <>
          <div>Single Selection List</div>
          <div className="mt-1 text-sm text-gray-600">
            Typically used for shorter lists so that all responses are visible
            as radio buttons. Only allow a single selection.
          </div>
        </>
      }
      onCancel={onCancel}
      onOk={handleOk}>
      <Form
        {...createFormItemLayout(3, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Form.Item label="Text" name="text">
          <Input />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
        <Form.Item label="Options" />
        <Form.Item noStyle name="options">
          <EditableTable
            columns={columns}
            form={form}
            onRowAdd={handleRowAdd}
            onRowDelete={handleRowDelete}
          />
        </Form.Item>
      </Form>
    </Modal>
  )
}
