import React from 'react'
import { ModalProps as AntModalProps } from 'antd/lib/modal'
import Modal from '@/components/modal'
import Button from '@/components/button'

export interface ModalProps<T = any>
  extends Omit<AntModalProps, 'onOk' | 'onCancel'> {
  item?: T
  onOk?: (data?: any) => void
  onCancel: () => void
  children?: React.ReactNode
}

function TemplateModal<T = any>({
  onCancel,
  onOk,
  item,
  children,
  ...props
}: ModalProps<T>) {
  const handleOk = () => {
    if (onOk) {
      onOk()
    }
  }
  return (
    <Modal
      visible
      onCancel={onCancel}
      width={650}
      maskClosable={false}
      keyboard={false}
      footer={[
        <Button
          key="btn-cancel"
          color="outline"
          style={{ minWidth: 80, marginRight: 16 }}
          onClick={onCancel}>
          取消
        </Button>,
        <Button
          key="btn-save"
          color="primary"
          style={{ minWidth: 80 }}
          onClick={handleOk}>
          儲存
        </Button>,
      ]}
      {...props}>
      {children}
    </Modal>
  )
}

export { TemplateModal as Modal }

export default TemplateModal
