import React from 'react'
import { Checkbox } from 'antd'
import { CheckboxProps } from 'antd/lib/checkbox'
import styled from 'styled-components'
import { RefProps } from '@/constants/types'

const StyledCheckbox = styled(Checkbox)`
  .ant-checkbox-checked .ant-checkbox-inner {
    background-color: ${p => p.theme.primary};
    border-color: ${p => p.theme.primary};
  }

  &.ant-checkbox-wrapper:hover .ant-checkbox-inner,
  .ant-checkbox:hover .ant-checkbox-inner,
  .ant-checkbox-input:focus + .ant-checkbox-inner {
    border-color: ${p => p.theme.primary};
  }

  .ant-checkbox-checked .ant-checkbox-inner:after {
    border: 2px solid #fff;
    border-top: 0;
    border-left: 0;
  }

  .ant-checkbox-inner {
    border: 1px solid ${p => p.theme.secondary};
  }

  &.is--normal {
    font-size: 14px !important;
    font-weight: normal !important;
  }
`

type Props = RefProps & CheckboxProps

const CheckboxComponent: React.ElementType<Props> = React.forwardRef<
  typeof StyledCheckbox,
  Props
>(({ ...props }, ref) => <StyledCheckbox forwardRef={ref} {...props} />)

CheckboxComponent.displayName = 'Checkbox'

export const CheckboxGroup = Checkbox.Group

export default CheckboxComponent
