import React from 'react'
import cx from 'classnames'
import { Card } from 'antd'
import { CardProps } from 'antd/lib/card'
import { FontColorsOutlined } from '@ant-design/icons'
import styled from 'styled-components'
import { useReportPreview } from '@/hooks/useReportTemplate'
import { isNil } from '@/utils/webHelper'

const StyledCard = styled(Card)`
  &.ant-card {
    border-radius: 10px;
    border: none;
    cursor: move;
  }

  .ant-card-head {
    padding-left: 18px;
    border-bottom: none;
    color: ${p => p.theme.primary};
    font-size: 14px;
    font-weight: 600;
  }

  &.ant-card-head--hide .ant-card-head {
    display: none;
  }

  &.is--preview .ant-card-head {
    min-height: auto;
  }

  &.is--preview .ant-card-head-title {
    padding-top: 0;
  }

  &.is--preview .ant-card-body {
    padding-top: 12px;
    padding-bottom: 8px;
  }

  .ant-card-head-title {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 0;
  }

  .ant-card-body {
    padding-top: 16px;
    padding-left: 26px;
    padding-bottom: 20px;
  }
`

interface Props extends CardProps {
  onEdit?: () => void
  onDelete?: () => void
}

function TemplateWrapper({
  title,
  children,
  onDelete,
  onEdit,
  ...props
}: Props) {
  const [isPreview] = useReportPreview()

  return (
    <StyledCard
      title={
        <div className="flex items-center">
          {!isPreview && <FontColorsOutlined className="mr-3" />}
          {title || '預設標題 (可點鉛筆圖示可進行編輯)'}
        </div>
      }
      className={cx({
        'ant-card-head--hide': isNil(title),
        'is--preview': isPreview,
      })}
      {...props}>
      {children}
    </StyledCard>
  )
}

export default TemplateWrapper
