import React from 'react'
import { atom, atomFamily, useRecoilState, useSetRecoilState } from 'recoil'
import {
  find,
  mergeAll,
  map,
  values,
  head,
  keys,
  path,
  pathOr,
  propEq,
} from 'ramda'
import moment from 'moment'
import fakeSetting from '@/mocks/fake-snippet-setting.json'
import { reportTemplateById } from '@/components/report-template-list'
const { compose } = require('ramda')
const faker = require('faker')

type SnippetList = { [key: string]: any[] }

export const reportTemplateCategory = {
  CLINICAL_HISTORY: 'Clinical History',
  HIGHT_RISK_REASONS: 'Hight Risk Reasons',
  DATA_OF_PRIOR_SIMILAR_STUDY: 'Data of prior similar study',
  IMAGING_PROTOCOL: 'Imaging Protocal',
  TUMOR_LOCATION: 'Tumor Location/Size',
}

const fakeSnippetSetting = compose(
  mergeAll,
  map((key: string) => ({
    [key]: compose(
      map((x: any) => ({
        ...x,
        Component: path([x.type, 'Component'])(reportTemplateById),
      })),
      pathOr([], [key])
    )(fakeSetting),
  })),
  keys
)(fakeSetting) as { [key: string]: any[] }

const fakeReportTemplateList = Array.from({ length: 100 }).map((_, idx) => ({
  id: idx + 1,
  name: faker.lorem.word(),
  author: faker.name.findName(),
  status: 1,
  statusName: '已發佈',
  createdAt: moment(),
  createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
  updatedAt: moment(),
  updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
  description: faker.lorem.word(),
  setting: fakeSnippetSetting,
}))

export const defaultSnippetList = compose(
  mergeAll,
  map((key: string) => ({ [key]: [] })),
  values
)(reportTemplateCategory)

export const reportSnippetsState = atomFamily<
  SnippetList,
  React.Key | undefined
>({
  key: 'reportSnippetsState',
  default: id =>
    compose(
      pathOr(defaultSnippetList, ['setting']),
      find(propEq('id', Number(id)))
    )(fakeReportTemplateList),
})

const snippetCategoryState = atom<string>({
  key: 'snippetCategoryState',
  default: compose(head, values)(reportTemplateCategory),
})

const reportTemplateListState = atom({
  key: 'reportTemplateListState',
  default: {
    content: fakeReportTemplateList,
    total: 100,
  },
})

const reportPreviewState = atom({
  key: 'reportPreviewState',
  default: false,
})

export function useReportPreview() {
  const [isPreview, setIsPreview] = useRecoilState(reportPreviewState)

  return [isPreview, () => setIsPreview(prev => !prev)] as [boolean, () => void]
}

export function useReportSnippets(id?: React.Key) {
  const [_snippetList] = useRecoilState(reportSnippetsState(id))

  React.useEffect(() => {
    setSnippetList(_snippetList)
  }, [JSON.stringify(_snippetList)]) // eslint-disable-line

  const [snippetList, setSnippetList] = React.useState<SnippetList>(
    _snippetList
  )

  return [snippetList, setSnippetList] as [
    SnippetList,
    React.Dispatch<React.SetStateAction<SnippetList>>
  ]
}

export function useSnippetCategory(values?: any) {
  const state = useRecoilState(snippetCategoryState)

  React.useEffect(() => {}, [])

  return state
}

export function useReportTemplateList() {
  return useRecoilState(reportTemplateListState)
}

export function useToggleReportPreview(state: boolean = false) {
  const setter = useSetRecoilState(reportPreviewState)

  React.useEffect(() => {
    setter(state)

    return () => {
      setter(false)
    }
  }, [state]) // eslint-disable-line
}
