import { Input } from 'antd'
import styled, { css } from 'styled-components'

const borderLess = css`
  &.ant-input-borderless {
    border: none;
    border-bottom: 1px solid ${p => p.theme.primary};
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &:active:not(.ant-input-borderless) {
    box-shadow: 0 0 0 1px ${p => p.theme.primary};
  }
`

const StyledInput = styled(Input)`
  &.ant-input-affix-wrapper .ant-input,
  &.ant-input {
    border-color: ${p => p.theme.primary};

    &:hover,
    &:focus {
      border-color: ${p => p.theme.secondary};
    }

    ${borderLess};
  }
`

const Search = styled(Input.Search)`
  &.ant-input-search {
    .ant-input {
      border-color: ${p => p.theme.primary};

      &:hover,
      &:focus {
        border-color: ${p => p.theme.secondary};
      }

      &:focus {
        box-shadow: 0 0 0 1px ${p => p.theme.primary};
      }
    }

    .anticon-search {
      font-size: 20px;
      color: ${p => p.theme.primary};
    }
  }
`

const TextArea = styled(Input.TextArea)`
  &.ant-input {
    resize: none;
    border: 1px solid ${p => p.theme.primary};

    &:hover,
    &:focus {
      border-color: ${p => p.theme.secondary};
    }

    ${borderLess};
  }
`

export { StyledInput as Input, TextArea, Search }

export default StyledInput
