import React from 'react'
import { Form } from 'antd'
import { pathOr } from 'ramda'
import Input from '@/components/input'
import { TimePicker } from '@/components/datepicker'
import { Modal, ModalProps } from '@/components/report-template-modal'
import TemplateWrapper from '@/components/report-template-wrapper'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'

interface Setting extends ReportTemplateSetting {
  defaultValue?: moment.Moment
}

export default function ReportTemplateTime(props: ReportTemplateProps) {
  const { editModalVisible, handleEditModalVisible, item, onUpdate } = props

  const [setting, setSetting] = React.useState<Setting>(
    pathOr({}, ['setting'], item)
  )

  const handleOk = (values: Setting) => {
    setSetting(values)
    onUpdate({ ...item, setting: values })
  }

  return (
    <>
      {editModalVisible && (
        <InputTimeModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <TimePicker
          bordered={false}
          className="w-1/3"
          placeholder={setting.tooltip || '選擇時間'}
          defaultValue={setting.defaultValue}
        />
      </TemplateWrapper>
    </>
  )
}

function InputTimeModal({
  onOk,
  onCancel,
  item,
  ...props
}: ModalProps<Setting>) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  return (
    <Modal<Setting>
      {...props}
      title={
        <>
          <div>
            Time
            <div className="inline ml-2 text-sm text-gray-600">
              typically used for target time.
            </div>
          </div>
        </>
      }
      onCancel={onCancel}
      onOk={handleOk}>
      <Form
        {...createFormItemLayout(5, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Form.Item label="Text" name="text">
          <Input />
        </Form.Item>
        <Form.Item label="Default Value" name="defaultValue">
          <TimePicker allowClear format="HH:mm" className="w-full" />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}
