import React from 'react'
import { Form } from 'antd'
import { pathOr } from 'ramda'
import Input from '@/components/input'
import InputNumber from '@/components/input-number'
import { Modal, ModalProps } from '@/components/report-template-modal'
import TemplateWrapper from '@/components/report-template-wrapper'
import { createFormItemLayout } from '@/utils/form'
import { ReportTemplateSetting, ReportTemplateProps } from '@/constants/types'

interface Setting extends ReportTemplateSetting {
  defaultValue?: number
  max?: number
  min?: number
  units?: string
}

export default function ReportTemplateInputNumber(props: ReportTemplateProps) {
  const { editModalVisible, handleEditModalVisible, item, onUpdate } = props

  const [setting, setSetting] = React.useState<Setting>(
    pathOr({}, ['setting'], item)
  )

  const handleOk = (values: Setting) => {
    setSetting(values)
    onUpdate({ ...item, setting: values })
  }

  return (
    <>
      {editModalVisible && (
        <InputNumberModal
          item={setting}
          onCancel={handleEditModalVisible}
          onOk={handleOk}
        />
      )}
      <TemplateWrapper title={setting.text || ''}>
        <InputNumber
          style={{ width: '33.333333%' }}
          className="ant-input-number-borderless"
          defaultValue={setting.defaultValue}
          min={setting.min}
          max={setting.max}
          formatter={value => `${setting.units || ''} ${value}`}
          placeholder={setting.tooltip || '輸入數值'}
        />
      </TemplateWrapper>
    </>
  )
}

function InputNumberModal({ onOk, onCancel, item, ...props }: ModalProps) {
  const [form] = Form.useForm()

  const handleOk = () => {
    form.validateFields().then(values => {
      if (onOk) {
        onOk(values)
      }
      onCancel()
    })
  }

  return (
    <Modal
      {...props}
      title={
        <>
          <div>
            Number
            <div className="inline ml-2 text-sm text-gray-600">
              typically used for input number.
            </div>
          </div>
        </>
      }
      onOk={handleOk}
      onCancel={onCancel}>
      <Form
        {...createFormItemLayout(5, 16)}
        labelAlign="left"
        form={form}
        initialValues={item}>
        <Form.Item label="Text" name="text">
          <Input />
        </Form.Item>
        <Form.Item label="Default Value" name="defaultValue">
          <Input />
        </Form.Item>
        <Form.Item label="Tooltip" name="tooltip">
          <Input />
        </Form.Item>
        <Form.Item label="Minimum Value" name="min">
          <InputNumber />
        </Form.Item>
        <Form.Item label="Maximum Value" name="max">
          <InputNumber />
        </Form.Item>
        <Form.Item label="Units" name="units">
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}
