// tslint:disable: quotemark no-implicit-dependencies no-shadowed-variable no-var-requires
import React from 'react'
import { TableProps } from 'antd/lib/table'
import { FormInstance } from 'antd/lib/form'
import Table from '@/components/table'

type EditableTableProps<T> = Omit<TableProps<T>, 'onChange'> & {
  value?: T[]
  form: FormInstance
  onChange?(changedValue: T[] | null): void
  onRowAdd: (form: FormInstance) => any
  onRowDelete: (value: { id: React.Key; form: FormInstance }) => any
}

export default function EditableTable<T = any>({
  form,
  value,
  onChange,
  onRowAdd,
  onRowDelete,
  ...props
}: EditableTableProps<T>) {
  const triggerChange = (changedValue: T[]) => {
    if (onChange) {
      onChange(changedValue)
    }
  }

  const handleAdd = () => {
    triggerChange(onRowAdd(form))
  }

  const handleDelete = (id: string) => {
    triggerChange(onRowDelete({ id, form }))
  }

  return (
    <Table
      {...props}
      rowKey="id"
      pagination={false}
      size="small"
      dataSource={value as any}
      onAddRowClick={handleAdd}
      onDeleteRowClick={handleDelete}
      editable={true}
    />
  )
}
