import React from 'react'
import { InputNumber } from 'antd'
import { InputNumberProps } from 'antd/lib/input-number'
import styled from 'styled-components'
import { RefProps } from '@/constants/types'
import { percent, currency, thosandSeprartor } from '@/utils/form'

type Props = RefProps & InputNumberProps & { bordered?: boolean }

const StyledInputNumber = styled(InputNumber).attrs({
  min: 0 as any, // dont know why error
})<Props>`
  &.ant-input-number {
    border-color: ${p => p.theme.primary};

    ::placeholder {
      color: red;
    }
  }

  &.ant-input-number-focused,
  &.ant-input-number:focus,
  &.ant-input-number:hover {
    border-color: ${p => p.theme.primary};
  }

  &.ant-input-number-focused:not(.ant-input-number-borderless),
  &.ant-input-number:focus:not(.ant-input-number-borderless) {
    box-shadow: 0 0 0 1px ${p => p.theme.primary};
  }

  &.ant-input-number-borderless,
  &.ant-input-number-borderless:hover,
  &.ant-input-number-borderless:focus,
  &.ant-input-number-borderless-focused,
  &.ant-input-number-borderless-disabled,
  &.ant-input-number-borderless[disabled] {
    border: none;
    border-bottom: 1px solid ${p => p.theme.primary};
  }
`

export const ThousandSeprartorInput = React.forwardRef<
  typeof InputNumber,
  Props
>(({ ...props }, ref) => (
  <StyledInputNumber forwardRef={ref} {...props} {...thosandSeprartor} />
))

export const CurrencyInput = React.forwardRef<typeof InputNumber, Props>(
  ({ ...props }, ref) => (
    <StyledInputNumber forwardRef={ref} {...props} {...currency} />
  )
)

export const PercentInput = React.forwardRef<typeof InputNumber, Props>(
  ({ ...props }, ref) => (
    <StyledInputNumber forwardRef={ref} {...props} {...percent} />
  )
)

ThousandSeprartorInput.displayName = 'ThousandSeprartorInput'

CurrencyInput.displayName = 'CurrencyInput'

PercentInput.displayName = 'PercentInput'

export { StyledInputNumber as InputNumber }

export default StyledInputNumber
