import React from 'react'
import styled, { css } from 'styled-components'
import { DatePicker, TimePicker } from 'antd'
import { DatePickerProps } from 'antd/lib/date-picker'

const borderLess = css`
  &.ant-picker-borderless {
    border: none;
    border-bottom: 1px solid ${p => p.theme.primary} !important;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }

  &.ant-picker-focused:not(.ant-picker-borderless) {
    box-shadow: 0 0 0 1px ${p => p.theme.primary};
  }
`

const RangePicker = styled(DatePicker.RangePicker)`
  &.ant-picker {
    border: 1px solid ${p => p.theme.primary};

    &:hover,
    &:focus {
      border-color: ${p => p.theme.secondary};
    }

    &.ant-picker-focused {
      box-shadow: 0 0 0 1px ${p => p.theme.primary};
    }
  }
`

const StyledDatePicker: any = styled(DatePicker)`
  &.ant-picker {
    border: 1px solid ${p => p.theme.primary};

    &:hover,
    &:focus {
      border-color: ${p => p.theme.secondary};
    }

    .ant-picker-suffix {
      color: ${p => p.theme.primary};
    }

    ${borderLess};
  }
`

const StyledTimePicker = styled(TimePicker)`
  &.ant-picker {
    border: 1px solid ${p => p.theme.primary};

    &:hover,
    &:focus {
      border-color: ${p => p.theme.secondary};
    }

    .ant-picker-suffix {
      color: ${p => p.theme.primary};
    }

    ${borderLess};
  }
`

function DatePickerComponent(props: DatePickerProps) {
  return <StyledDatePicker {...props} />
}

export {
  DatePickerComponent as DatePicker,
  RangePicker,
  StyledTimePicker as TimePicker,
}

export default DatePickerComponent
