import React from 'react'
import { Table, Empty, Tooltip } from 'antd'
import { TableProps } from 'antd/lib/table'
import { CloseOutlined, PlusOutlined } from '@ant-design/icons'
import styled from 'styled-components'

const StyledTable: any = styled(Table)`
  &.ant-table-wrapper {
    .ant-pagination-prev .ant-pagination-item-link,
    .ant-pagination-next .ant-pagination-item-link {
      border-color: ${p => p.theme.pagination};
      border-radius: 6px;
      color: ${p => p.theme.pagination};
    }

    .ant-pagination-item {
      border-color: ${p => p.theme.pagination};
    }

    .ant-pagination-item a {
      color: ${p => p.theme.pagination};
    }

    .ant-pagination-item-active,
    .ant-pagination-item-active:focus,
    .ant-pagination-item:hover {
      border-color: ${p => p.theme.pagination};
      background: ${p => p.theme.pagination};

      a {
        color: #fff;
      }
    }

    .ant-table-pagination.ant-pagination {
      margin-top: 32px;
    }

    .ant-pagination-prev:hover .ant-pagination-item-link,
    .ant-pagination-next:hover .ant-pagination-item-link {
      border-color: ${p => p.theme.pagination};
      background: ${p => p.theme.pagination};
      color: #fff;
    }

    .ant-pagination-simple-pager {
      color: ${p => p.theme.pagination};
    }
  }

  .ant-table-tbody > tr > td {
    height: 40px;
    padding-top: 10px;
    padding-bottom: 10px;
    line-height: 1em;
    color: #4a4a4a;
    background: #f0f2f5;
    border-bottom: 1px solid #e9e9e9;
  }

  .ant-table-thead > tr > th {
    background: rgb(196, 213, 241);
    border-bottom: 1px solid #e9e9e9;
    color: ${p => p.theme.primary};
    font-weight: 600;
    font-size: 16px;
  }
`

type Props<T> = TableProps<T> & {
  editable?: boolean
  isRowDeletable?: (row: T) => boolean
  onAddRowClick?: (param?: any) => any
  onDeleteRowClick?: (param?: any) => any
  isCanEdit?: any
}

function TableComponent<T = any>({
  editable = false,
  columns = [],
  onAddRowClick,
  isRowDeletable = () => true,
  onDeleteRowClick,
  isCanEdit = '',
  ...props
}: Props<T>) {
  const _columns = React.useMemo(() => {
    let canEdit: boolean = true
    if (!editable) {
      return columns
    }
    if (isCanEdit !== '') {
      canEdit = isCanEdit
    }

    const handleRowDelete = (key: React.Key) => () => {
      if (onDeleteRowClick) {
        onDeleteRowClick(key)
      }
    }

    return [
      {
        title: (
          <Tooltip placement="topLeft" title="新增">
            <PlusOutlined
              className="ml-1 relative"
              style={{ top: -3 }}
              onClick={canEdit ? onAddRowClick : undefined}
            />
          </Tooltip>
        ),
        width: 50,
        render: (_: any, __: any, idx: number) => (
          <div className="text-center">#{idx + 1}</div>
        ),
      },
      ...columns,
      {
        width: 50,
        render: (_: any, r: any, idx: number) => (
          <Tooltip placement="topLeft" title="刪除">
            <CloseOutlined
              onClick={isRowDeletable(r) ? handleRowDelete(r.id) : undefined}
            />
          </Tooltip>
        ),
      },
    ]
  }, [columns, editable]) // eslint-disable-line

  return (
    <StyledTable
      columns={_columns}
      locale={{
        emptyText: (
          <Empty
            image={Empty.PRESENTED_IMAGE_SIMPLE}
            description={
              <span>
                <span className="d-block">
                  尚無資料，請點選「＋新增」新增資料。
                </span>
              </span>
            }
          />
        ),
      }}
      {...props}>
      {props.children}
    </StyledTable>
  )
}

TableComponent.displayName = 'Table'
TableComponent.Column = Table.Column

export default TableComponent
