import React from 'react'
import { atom, useRecoilState, useRecoilValue, useSetRecoilState } from 'recoil'

type BreadcrumbItem = {
  href?: string
  name: string
}

export const breadcrumbState = atom<BreadcrumbItem[]>({
  key: 'breadcrumbState',
  default: [],
})

export function useBreadcrumbState() {
  const state = useRecoilState(breadcrumbState)
  return state
}

export function useBreadcrumb(items: BreadcrumbItem[] = []) {
  const setter = useSetRecoilState(breadcrumbState)

  React.useEffect(() => {
    setter(items)
  }, []) // eslint-disable-line
}

export const headerContentState = atom<React.ReactNode>({
  key: 'headerContentState',
  default: null,
})

export function useHeaderContentState() {
  return useRecoilState(headerContentState)
}

export function useHeaderContent(node?: React.ReactNode) {
  const setter = useSetRecoilState(headerContentState)

  React.useEffect(() => {
    setter(node)

    return () => {
      setter(null)
    }
  }, []) // eslint-disable-line
}

export function useHeaderContentValue() {
  const value = useRecoilValue(headerContentState)
  return value
}
